    .square {
        /* border: 5px solid rgb(139, 0, 30); */
        display: inline-block;
        /* margin-top: 100px; margin-left: 50px; */
        margin: 20px;
        border-radius: 10px;
        background-color: rgb(165, 173, 184);
        box-shadow: 50px;
        background-image: url("./check\ id.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        -webkit-box-shadow: 10px 10px 16px 14px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 10px 10px 16px 14px rgba(219, 192, 192, 0.75);
        box-shadow: 10px 10px 16px 14px rgba(0, 0, 0, 0.75);
    }
    
    .square table {
        padding: 5px;
        display: inline-block;
    }
    
    .square th {
        color: indianred;
        font-family: sans-serif;
    }
    
    .square td {
        color: indigo;
        font-family: sans-serif;
    }