    .beautify table,
    td {
        margin-top: 1%;
        color: darkmagenta;
        font-family: cursive;
        border: 1px solid blue;
        border-radius: 10px;
        border-collapse: collapse;
        padding: 3px;
    }
    
    th {
        color: red;
        border: 1px solid blue;
    }
    
    .beautify button {
        color: red;
        background-color: royalblue;
        margin: 5px;
        border: 2px solid black;
        margin-top: 5%;
    }
    
    .beautify select {
        margin-right: 5px;
        margin: 5px;
    }
    
    .beautify input {
        padding: 6px;
        width: 50%;
    }
    
    .beautify label {
        color: mediumvioletred;
    }